.dashboard {
  min-height: 99vh;
  padding: 70px;

  background-color: #f8f8f8;
}

.dashboard__content {
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
}

.dashboard__item {
  margin-bottom: 30px;
  padding: 30px;

  background-color: white;
  border-radius: 10px;
  box-shadow: 0 2px 0 rgba(0, 0, 0, .09);
}

.analysis__title {
  color: @text-back;
  font-size: 30px;
  font-weight: 700;
}

.analysis__title--add {
  margin-bottom: 25px;
}

.analysis__title--fin {
  color: #625785;
}

.analysis__form {
  display: flex;
  justify-content: space-between;
}

.accordion-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.accordion-header--open {
  margin-bottom: 30px;
}

.analysis__bar-wrap {
  margin-left: 20px;
}

.analysis__bar-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.analysis__percent {
  color: @text-front;
  font-weight: 700;
}

.analysis__followers {
  color: @text;
}

.analysis__bar {
  width: 400px;
  height: 6px;

  background-color: #ebebeb;
  border-radius: 3px;
}

.analysis__bar-fill {
  display: block;
  height: 6px;

  background-color: #a6d48a;
  border-radius: 3px;
}

.analysis__list {
  width: 205px;
  margin-right: 20px;
}

.analysis__list-title {
  margin-bottom: 5px;

  color: @text;
}

.analysis__list-btn {
  padding: 0;

  color: @active;
  border: none;
  background: none;
  border-bottom: 1px solid;
}

.analysis__list-items {
  display: none;

  color: @text-front;
  line-height: 1.5;
  text-overflow: ellipsis;
  overflow: hidden;
}

.accordion__open-btn {
  width: 30px;
  height: auto;
  align-self: stretch;

  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../img/accordion-arr.svg);
}

.accordion__close-btn {
  display: none;
  width: 290px;
  height: 53px;

  color: @text-front;
  font-size: 18px;
  font-weight: 700;
  border: none;
  background-color: #ebebeb;
  border-radius: 4px;
}

.accordion-content {
  display: none;
  margin-top: 30px;
}

.accounts {
  margin: 40px -30px 30px;
}

.account-item {
  display: flex;
  align-items: center;
  padding: 10px 30px;

  border-top: 1px solid #eee;

  &:last-of-type {
    border-bottom: 1px solid #eee;
  }
}

.account__img {
  img {
    border-radius: 50%;
  }
}

.account__info {
  width: 35%;
  margin-left: 30px;
}

.account__username {
  display: block;
  margin-bottom: 10px;

  color: @active;
  font-size: 18px;
  font-weight: 700;
}

.account__followers,
.account__involvement {
  margin-left: 40px;
}

.account__title {
  margin-bottom: 10px;

  color: @text;
  font-size: 14px;
}

.account__count {
  font-size: 18px;
}

.analysis__management {
  display: flex;
  justify-content: space-between;
}

.analysis__all {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 290px;
  height: 54px;

  color: @active;
  font-size: 18px;
  font-weight: 700;
  border-radius: 4px;
  border: 2px solid;
}

.analysis__repeat {
  font-size: 18px;
}
