*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

a,
button,
input,
textarea {
  outline: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

textarea {
  resize: vertical;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
