.site-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 50px;
}

.header__user {
  margin-right: auto;
}

.header__link {
  margin-left: 60px;

  color: @text;
  font-size: 14px;

  + .header__link {
    margin-left: 30px;
  }
}

.header__link--username {
  color: #29233b;
  font-size: 18px;
  font-weight: 700;
}

.header__divider {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: -30px;
    width: 1px;
    height: 30px;
    margin: auto;

    background-color: #f3f3f3;
  }
}

.header__balance {
  .header__link {
    color: #29233b;
  }
}

.header__balance-count {
  font-weight: 700;
}

.refill {
  .header__link;
  .link;

  border-bottom: 1px solid;
}
