.auth-main {
  display: flex;
}

.auth-left,
.auth-right {
  display: flex;
  align-items: center;
  min-height: 100vh;
  width: 50%;
  padding: 10px;
}

.auth-left {
  flex-direction: column;
  justify-content: center;

  background-color: #231e32;
}

.auth-right {
  padding-left: 80px;

  background-color: #1e1a2a;
}

.auth-text__item {
  max-width: 485px;
  margin-top: 50px;

  line-height: 27px;
}

.auth-text {
  color: @text;
}

.auth-footer {
  width: 485px;
  margin-top: 15vh;

  color: @text;
  font-size: 14px;
}

.auth-form {
  max-width: 390px;
}

.pass-restore-link {
  position: absolute;
  top: 41%;
  right: 20px;

  color: #404040;
  font-size: 14px;

  transform: translateY(10px);

  + .input {
    padding-right: 80px;
  }
}

.label--captcha {
  img,
  input {
    width: 190px;
    display: inline-block;
    vertical-align: bottom;
  }

  img {
    height: 54px;
    margin-right: 6px;

    border-radius: 4px;
  }
}

.label--checkbox {
  margin-top: 30px;
  margin-bottom: 0;
}
