.wrap {
  .container();
}

.link {
  color: @active;
}

/*
 * buttons
 */

.btn {
  width: 290px;
  height: 54px;
  border: none;
  &[disabled] {
    color: #ffffff;
    background-color: #cfcfcf;
  }
}

.btn--link {
  background: none;
}

.btn--active {
  color: @text-auth;
  font-weight: 700;
  background-color: @active;
  border-radius: 4px;
}

.btn--auth {
  width: 390px;
  height: 54px;
}


/*
 * headings
 */

.h1 {
  margin-bottom: 15px;

  color: #fefefe;
  font-size: 30px;
  font-weight: 300;
}

.h2, .h3 {
  color: @text-auth;
  font-weight: 700;
}

.h2 {
  margin-bottom: 10px;

  font-size: 26px;
  font-weight: 700;
}

.h-sub {
  display: inline-block;
  margin-bottom: 35px;

  color: #ffffff;
  font-weight: 300;
}

/*
 * form
 */

.label {
  position: relative;
  display: block;
  margin-bottom: 30px;

  color: #9d9d9d;
  font-size: 14px;

  cursor: pointer;
}

.input,
textarea {
  display: block;
  margin-top: 5px;
  padding: 20px;

  color: #2c2c2c;
  font-weight: 700;
  background-color: #f8fafc;
  border: 1px solid #dfe3e9;
  border-radius: 4px;
}

.input {
  width: 290px;
  height: 54px;
}

.input--auth {
  width: 390px;
}

textarea {
  width: 520px;
  height: calc(~'100% + 8px');

  resize: none;
}

.jq-checkbox {
  top: -1px;
  width: 16px;
  height: 16px;
  vertical-align: middle;
  margin-right: 15px;
  cursor: pointer;

  background-color: white;
  border: 1px solid #dfe3e9;
  border-radius: 2px;
}

.jq-checkbox.checked .jq-checkbox__div {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 8px;
  height: 8px;
  margin: auto;

  background-color: @active;
  border-radius: 2px;
}
